.advanced-search {
  position: relative;
  float: right;
  margin: 20px;
}
.advanced-search .search-input {
    width: 360px;
    height: 32px;
    padding: 0 32px 0 10px;
    font-size: 14px;
    color: #666666;
    border: 1px solid #c3cdd7;
    line-height: 32px;
    outline: none;
}
.advanced-search .search-input:hover {
      border-color: #c0c4cc;
}
.advanced-search .search-input:focus {
      border-color: #3c96ff;
}
.advanced-search .search-input.placeholder {
    color: #c3cdd7;
}
.search-content {
  margin: 30px;
  color: #313238;
  font-size: 14px;
  font-weight: 400;
  cursor: pointer;
}
.search-content:hover {
    color: #3c96ff;
}
.search-content .advanced-shape {
    display: inline-block;
    margin-left: 5px;
    font-size: 12px;
    color: #cccccc;
}
.search-content .advanced-shape:hover {
      color: #3c96ff;
}
.common-icon-search {
  position: absolute;
  right: 15px;
  top: 8px;
  color: #63656e;
}
